import React, { useState } from 'react';
import MenuIcon from '@material-ui/icons/Menu';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { Typography, AppBar, Toolbar, IconButton, Grid, Menu, MenuItem, Button } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import moment from 'moment'
const Header = () => {
    const [anchorEl, setAnchorEl] = useState(null);
   
    const theme = useTheme();
    const { enqueueSnackbar } = useSnackbar();
    const isSmall = useMediaQuery(theme.breakpoints.down('sm'));

    const openWebsite = () => window.open("https://axion.network", "_blank");
    const openUniswap = () => window.open("https://app.uniswap.org/#/swap?outputCurrency=0xed1199093b1abd07a368dd1c0cdc77d8517ba2a0", "_blank");
    const openReferral = () => enqueueSnackbar("Coming soon!", { variant: 'info', anchorOrigin: { vertical: 'bottom', horizontal: 'right', autoHideDuration: 3000 }}); //window.open("https://discord.gg/axion", "_blank");

    const handleClick = (event) => { setAnchorEl(event.currentTarget) }
    const handleClose = () => { setAnchorEl(null) }

    return (
        <div>
            <AppBar position="static" className="navBar" elevation={10} >
                <Toolbar>

                    <Grid container justify="space-between" alignItems="center">
                        <Grid item>
                            <Typography variant="h5" align="center" color="primary" style={{ fontWeight: '100', color: "#FFF" }}>Axion Stats</Typography>
                        </Grid>
                        <Grid item>
                            {!isSmall && (
                                <div className="headerLinks">
                                    <Typography onClick={openWebsite} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink noselect">Axion Website</Typography>
                                    <Typography onClick={openUniswap} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink noselect">Buy Axion (Uniswap)</Typography>

                                    <Button onClick={openReferral} variant="contained" color="secondary" className="referralButton">
                                        <Typography variant="subtitle2">Buy Axion, Get 10% Bonus</Typography>
                                    </Button>
                                </div>
                            )}

                            {isSmall && (<>
                                <IconButton edge="start" color="inherit" onClick={handleClick}>
                                    <MenuIcon />
                                </IconButton>

                                <Menu anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleClose}>
                                    <MenuItem onClick={openWebsite}>Axion Website</MenuItem>
                                    <MenuItem onClick={openUniswap}>Buy Axion</MenuItem>
                                </Menu>
                            </>)}
                        </Grid>
                    </Grid>

                </Toolbar>
            </AppBar>
        </div>
     
    
        // <div className="header">
        //     <Typography align="center" variant="subtitle2" style={{ color: "#FFF" }}>Beta {VERSION} - Created by<span style={{ color: '#00D637' }}> Some Green Guy</span></Typography>
            
        //     <div className="headerLinks">
        //         <Typography onClick={openWebsite} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink">Axion Website</Typography>
                
        //         <span style={{ color: "#BABABA" }}>&nbsp;|&nbsp;</span>
                
        //         <Typography onClick={openWhitepaper} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink">Whitepaper</Typography>
                
        //         <span style={{ color: "#BABABA" }}>&nbsp;|&nbsp;</span>
                
        //         <Typography onClick={openDiscord} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink">Discord</Typography>

        //         <span style={{ color: "#BABABA" }}>&nbsp;|&nbsp;</span>

        //         <Typography onClick={openUniswap} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink">Uniswap</Typography>
        //     </div>
        // </div>
    )
}

export default Header;